.ProCont {
  background-color: rgb(0, 0, 0);
  margin-top: 60px;
  padding-bottom: 80px;
}

.ProCont .ExpHeader {
  padding-top: 20px;
}

.ProContent {
  width: 85%;
  height: 100%;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 3rem;
  opacity: 0;
}

.ProContent.fade-in {
  animation: fade 4s;
  opacity: 1;
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.moreProject {
  display: flex;
  align-items: center;
  width: 80%;
  margin: auto;
}

@media screen and (max-width: 1280px) and (min-width: 961px) {
  .ProContent {
    width: 90%;
  }

}

@media screen and (max-width: 960px) {
  .ProCont .ExpHeader {
    padding-top: 40px;
  }

  .ProCont .ExpSubHeader {
    padding-bottom: 15px;
  }

  
.moreProject { 
  width: 100%;
  margin: 0;
  justify-content: center;
}
}
